import React, {useEffect, useRef} from 'react';
import Clappr from 'clappr';

const ClapprPlayer = ({source, width = '100%', height = '70vh'}) => {
    const playerRef = useRef(null);

    useEffect(() => {
        // Kim tra xem playerRef  tn ti cha
        if (!playerRef.current) return;

        // Khi to player Clappr
        const player = new Clappr.Player({
            source: source,
            parent: playerRef.current,
            width: width,
            height: height,
        });

        // Cleanup: hy player khi component unmount
        return () => {
            player.destroy();
        };
    }, [source, width, height]); // Gi li khi source, width hoc height thay i

    return <div ref={playerRef}/>; // S dng useRef  gn ref cho div
};

export default ClapprPlayer;
